/* Global Styles */
@import url("https://fonts.googleapis.com/css?family=Comfortaa|Nanum+Pen+Script&display=swap");
.modal-dialog {
  margin: 7rem auto 1.75rem; }

:root {
  /*--primary-color: #fc6459;*/
  --primary-color: #e00083;
  --secondary-color: #0892ab;
  /*--tertiary-color: #ff5a5f;*/
  --tertiary-color: #e00083;
  --dark-color: #0892ab;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
  --white-color: #fff;
  --light-grey-color: #bbb;
  --large-text-color: #444; }

