/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 0.6rem;
  line-height: 1.6;
  background-color: #fff;
  color: #444;
  overflow-x: auto;
  padding: 10px; }

.modal-body {
  font-size: 0.8rem; }

#main {
  float: left;
  margin-left: 20px; }

a {
  color: var(--primary-color);
  text-decoration: none; }

ul {
  list-style: none; }

img {
  width: 100%; }

/* Utilities */
.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
  margin-top: 1rem;
  margin-bottom: 3rem; }

/* Text Styles*/
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 0.8rem; }

.large {
  font-size: 2rem;
  line-height: 1.2;
  margin-bottom: 0.8rem; }

.lead {
  font-size: 1.2rem;
  margin-bottom: 0.8rem; }

.text-center {
  text-align: center; }

.text-primary {
  color: var(--large-text-color); }

.text-dark {
  color: var(--dark-color); }

/* Padding */
.p {
  padding: 0.5rem; }

.p-1 {
  padding: 1rem; }

.p-2 {
  padding: 2rem; }

.p-3 {
  padding: 3rem; }

.py {
  padding: 0.5rem 0; }

.py-1 {
  padding: 1rem 0; }

.py-2 {
  padding: 2rem 0; }

.py-3 {
  padding: 3rem 0; }

/* Margin */
.m {
  margin: 0.5rem; }

.m-1 {
  margin: 1rem; }

.m-2 {
  margin: 2rem; }

.m-3 {
  margin: 3rem; }

.my {
  margin: 0.5rem 0; }

.my-1 {
  margin: 1rem 0; }

.my-2 {
  margin: 2rem 0; }

.my-3 {
  margin: 3rem 0; }

.m-left-5 {
  margin-left: 5px; }

.btn {
  display: inline-block;
  background: var(--light-color);
  color: #333;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none; }

.badge {
  font-size: 0.8rem;
  padding: 0.1rem;
  text-align: center;
  margin: 0.3rem;
  background: var(--light-color);
  color: #333; }

.alert {
  padding: 0.8rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: var(--light-color);
  color: #333; }

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--primary-color);
  color: #fff; }
  .btn-primary:hover, .btn-primary:active, .btn-primary:focus,
  .bg-primary:hover,
  .bg-primary:active,
  .bg-primary:focus,
  .badge-primary:hover,
  .badge-primary:active,
  .badge-primary:focus,
  .alert-primary:hover,
  .alert-primary:active,
  .alert-primary:focus {
    background: #a10053; }

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333; }

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: var(--dark-color);
  color: #fff; }

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff; }

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: var(--success-color);
  color: #fff; }

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: var(--white-color);
  color: #333; }

.btn:hover {
  opacity: 0.8; }

.bg-light,
.badge-light {
  border: #ccc solid 1px; }

.round-img {
  border-radius: 50%; }

.line {
  height: 1px;
  background: #ccc;
  margin: 1.5rem 0; }

/* Overlay */
.dark-overlay {
  /*background-color: rgba(0, 0, 0, 0.7);
  position: absolute;*/
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

/* Forms */
.form .form-group {
  margin: 1.2rem 0; }

.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888; }

.form input[type='text'],
.form input[type='email'],
.form input[type='password'],
.form input[type='date'],
.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 0.8rem;
  border: 1px solid #ccc; }

.form input[type='submit'],
button {
  font: inherit; }

.table th,
.table td {
  padding: 1rem;
  text-align: left; }

.table th {
  background: var(--light-color); }

/* Landing Page */
.landing {
  position: relative;
  background: url("./img/show.png") no-repeat center center/cover;
  height: 100vh; }

.landing-inner {
  color: #1d1d1d;
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; }

/* Profiles Page */
.profile {
  display: grid;
  grid-template-columns: 2fr 4fr 2fr;
  align-items: center;
  grid-gap: 2rem;
  padding: 1rem;
  line-height: 1.8;
  margin-bottom: 1rem; }

/* Profile Page */
.profile-grid {
  display: grid;
  grid-template-areas: 'top top' 'about about' 'exp edu' 'github github';
  grid-gap: 1rem; }

.profile-top {
  grid-area: top;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; }

.profile-top img {
  width: 50px; }

.profile-top .icons a {
  color: #fff;
  margin: 0 0.3rem; }

.profile-top .icons a:hover {
  color: var(--dark-color); }

.profile-about {
  grid-area: about;
  text-align: center; }

.profile-about .interests {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center; }

/* Mobile Styles */
@media (max-width: 700px) {
  .container {
    margin-top: 8rem; }
  .hide-sm {
    display: none; }
  /* Text Styles */
  .x-large {
    font-size: 3rem; }
  .large {
    font-size: 2rem; }
  .lead {
    font-size: 0.8rem; }
  /* Navbar */
  .navbar {
    display: block;
    text-align: center; }
  .navbar ul {
    text-align: center;
    justify-content: center; }
  .navbar h1 {
    margin-bottom: 1rem; }
  .navbar .welcome {
    display: none; }
  /* Profiles Page */
  .profile {
    grid-template-columns: 1fr;
    text-align: center; }
  .profile ul {
    display: none; }
  /* Profile Page */
  .profile-top img,
  .profile img {
    width: 200px;
    margin: auto; }
  .profile-grid {
    grid-template-areas: 'top' 'about' 'exp' 'edu' 'github'; }
  .profile-about .interests {
    flex-direction: column; }
  .dash-buttons a {
    display: block;
    width: 100%;
    margin-bottom: 0.2rem; }
  .post {
    grid-template-columns: 1fr; }
  .post a,
  .post button {
    padding: 0.3rem 0.4rem; } }

.post-create-textarea {
  -moz-appearance: textfield-multiline;
  -webkit-appearance: textarea;
  color: #676e7a;
  font: medium -moz-fixed;
  font: -webkit-small-control;
  height: 40px;
  overflow: auto;
  padding: 2px;
  resize: both;
  width: 100%;
  padding: 0.4rem;
  font-size: 0.8rem;
  border: 1px solid #ccc;
  margin-bottom: 20px; }

.bg-post-head {
  background-color: #f5f6f7; }

.react-autosuggest__container {
  position: relative; }

.react-autosuggest__input {
  width: 240px;
  height: 30px;
  padding: 10px 20px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
  -webkit-appearance: none; }

.react-autosuggest__input--focused {
  outline: none; }

.react-autosuggest__input::-ms-clear {
  display: none; }

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0; }

.react-autosuggest__suggestions-container {
  display: none; }

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 30px;
  width: 280px;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2; }

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px; }

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd; }

[class*='fontawesome-']:before {
  font-family: 'fontawesome', sans-serif; }

.select-search-box {
  width: 300px;
  position: relative;
  margin-bottom: 20px;
  background: #fff;
  border-radius: 4px; }

.select-search-box--multiple {
  border-radius: 4px;
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07);
  overflow: hidden; }

.select-search-box::after {
  font-family: fontawesome;
  content: '\f078';
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  text-align: center;
  line-height: 50px;
  color: #222f3e;
  z-index: 1; }

.select-search-box--multiple::after,
.select-search-box--focus::after {
  content: '\f002'; }

.select-search-box__icon--disabled::after {
  content: none !important; }

.select-search-box--input::after {
  display: none !important; }

.select-search-box__out {
  display: none; }

.select-search-box__search {
  display: block;
  width: 100%;
  height: 50px;
  border: none;
  background: none;
  outline: none;
  font-size: 16px;
  padding: 0 20px;
  color: #222f3e;
  -webkit-appearance: none;
  box-sizing: border-box;
  position: relative;
  z-index: 2;
  cursor: pointer;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 50px;
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07); }

input.select-search-box__search {
  line-height: 1; }

.select-search-box--multiple .select-search-box__search {
  box-shadow: none; }

.select-search-box--input .select-search-box__search {
  cursor: text; }

.select-search-box__search:focus {
  cursor: text; }

.select-search-box__search--placeholder {
  font-style: italic;
  font-weight: normal; }

.select-search-box input::-webkit-input-placeholder {
  color: #ccc;
  font-style: italic;
  font-weight: normal; }

.select-search-box input::-moz-placeholder {
  color: #ccc;
  font-style: italic;
  font-weight: normal; }

.select-search-box input:-moz-placeholder {
  color: #ccc;
  font-style: italic;
  font-weight: normal; }

.select-search-box input:-ms-input-placeholder {
  color: #ccc;
  font-style: italic;
  font-weight: normal; }

input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none; }

.select-search-box__select {
  display: none;
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  background: #fff;
  border-radius: 4px;
  overflow: auto;
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07);
  z-index: 100;
  min-height: 49px; }

.select-search-box--multiple .select-search-box__select {
  display: block;
  position: static;
  border-top: 1px solid #eee;
  border-radius: 0;
  box-shadow: none; }

.select-search-box__select--display {
  display: block; }

.select-search-box__option {
  font-size: 16px;
  font-weight: 400;
  color: #616b74;
  padding: 15px 20px;
  border-top: 1px solid #eee;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  user-select: none; }

.select-search-box__option:first-child {
  border-top: none; }

.select-search-box__option--hover,
.select-search-box__option:hover {
  background: #f4f7fa; }

.select-search-box__option--selected {
  background: #54a0ff;
  color: #fff;
  border-top-color: #2184ff; }

.select-search-box__option--selected:hover,
.select-search-box__option--selected.select-search-box__option--hover {
  background: #2184ff;
  color: #fff;
  border-top-color: #2184ff; }

.select-search-box__group {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #eee;
  position: relative; }

.select-search-box__group-header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 0 10px;
  color: rgba(0, 0, 0, 0.5);
  font-size: 12px; }

/* overriding semantic ui tab default style */
.ui.secondary.pointing.menu {
  border-bottom: 1px solid #f0f0f0; }
  .ui.secondary.pointing.menu .active.item {
    border-color: #ea4c89; }
    .ui.secondary.pointing.menu .active.item:hover {
      border-color: #ea4c89; }
